.grid{
  position: absolute;
  z-index: 5;
  background: var(--cover);
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
}
.form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 40%;
  height: 45%;
  border-radius: 30px;
  border: var(--border);
  background: var(--white);
  box-shadow: var(--shadow2);
}

.input{
  display: block;
  width: 90%;
  margin: 10px 0px;
}

.description{
  height: 100px;
  width: 90%;
}

.tags{
  display: flex;
  width: 50%;
  margin: 10px 0px;
}

.grid button{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  width: 150px;
  border-radius: 25px;
  border: none;
  background:var(--red);
  color: var(--pink)
}